
p span[data-v-2516dbac]{
	font-size: 22px;
}
.btn[data-v-2516dbac]{
	height: 30px;
	border: 1px solid #ccc;
	border-radius: 10%;
	background-color: #B3A86A;
	outline: 0;
	color: #faffaf;
	font-size: 20px;
}
.player[data-v-2516dbac]{
	margin:50px auto;
	width:200px;
	height: 200px;
	background-color: #666;
	border-radius: 50%;
	position: relative;
}
.run[data-v-2516dbac]{
	-webkit-animation: run 3s linear infinite ;
	        animation: run 3s linear infinite ;
}
.runPaused[data-v-2516dbac]{
	-webkit-animation: run 3s linear infinite paused;
	        animation: run 3s linear infinite paused;
}
.player[data-v-2516dbac]:after{
	content: '';
	position: absolute;
	top:0;
	bottom: 0;
	right: 0;
	left: 0;
	border-radius: 50%;
	margin:auto;
	width: 33%;
	height: 35%;
	background-color: #262626;
}
.arrow[data-v-2516dbac]{
	width:2px;
	height: 60px;
	top: 10px;
	left: 50%;
	margin-left: -1px;
	position: absolute;
	border-radius: 50%;
	background-color: #c7dbc1;
}
@-webkit-keyframes run{
0%{-webkit-transform:rotate(0) /*scale3d(1,1,1)*/;transform:rotate(0) /*scale3d(1,1,1)*/;
}
100%{-webkit-transform:rotate(360deg) /*scale3d(1.1,1.1,1.1)*/;transform:rotate(360deg) /*scale3d(1.1,1.1,1.1)*/;
}
}
@keyframes run{
0%{-webkit-transform:rotate(0) /*scale3d(1,1,1)*/;transform:rotate(0) /*scale3d(1,1,1)*/;
}
100%{-webkit-transform:rotate(360deg) /*scale3d(1.1,1.1,1.1)*/;transform:rotate(360deg) /*scale3d(1.1,1.1,1.1)*/;
}
}



